.main{
  background-size: cover;
  background-position: center;
  color: #fff;
  &__inner{
    min-height: 61.25em;
    padding: 17.5em 0 23.125em;
    position: relative;
  }
  &__content{
    max-width: 40em;
  }
  &__desc{
    margin-top: 1.375em;
    opacity: .8;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
  }
  &__btn{
    box-shadow: inset 0 .25em 1.125em rgba(255, 255, 255, 0.12), 0 3px 22px 0 rgba(248, 64, 74, 0.44), 0 3px 0 0 rgba(248, 64, 74, 0.44);
  }
}

.main-info{
  position: absolute;
  right: 0;
  bottom: 6.5em;
  width: 35.6875em;
  border-radius: .625em;
  padding: 2em;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    backdrop-filter: blur(10.199999809265137px);
    background: linear-gradient(136.74deg, rgba(255, 255, 255, 0.18) -3.483%,rgba(217, 217, 217, 0.06) 109.452%);
  }
  &__inner{
    position: relative;
    z-index: 2;
    color: rgba(255, 255, 255, 0.9);
  }
}

.new-products{
  padding: 5em 0 2.5em;
  &__grid{
    margin-top: 2.625em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.certificate-card,
.product-card{
  display: block;
  &__img{
    @include imgCover(60.8%);
    border-radius: .625em;
  }
}

.certificates{
  padding: 2.5em 0 1.5em;
}

.slider{
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.25em;
      height: auto;
    }
    &-button{
      height: 2em;
      width: 2em;
      @include center;
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      svg{
        width: 1em;
      }
      &-prev{
        left: -3em;
      }
      &-next{
        right: -3em;
      }
    }
  }
}

.certificates-slider{
  margin-top: 3.125em;
  .swiper{
    &-slide{
      width: calc(100% / 3);
    }
  }
}

.certificate-card{
  &__img{
    padding-bottom: 140.3%;
  }
}

@media screen and (max-width: 992px){
  .main{
    &__inner{
      min-height: unset;
      padding: 6em 0 2em;
    }
    &__content{
      max-width: 26em;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .main-info{
    position: relative;
    width: 100%;
    margin-top: 1.5em;
    bottom: unset;
    padding: 1.25em;
  }

  .new-products{
    padding: 3em 0 1.5em;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .certificates{
    padding: 1.5em 0 1em;
    overflow: hidden;
  }

  .slider{
    &__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        padding-right: .625em;
      }
      &-button{
        display: none;
      }
    }
  }

  .certificates-slider{
    margin-top: 1.5em;
    .swiper{
      &-slide{
        width: 38%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__btn{
      width: 100%;
    }
  }

  .main-info{
    padding: 1em;
  }

  .new-products{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .certificates-slider{
    .swiper{
      &-slide{
        width: 15em;
      }
    }
  }
}
