.delivery{
  padding: 0 0 3.125em;
  .map{
    height: 28.125em;
    margin-bottom: 3.75em;
  }
  .text{
    h2{
      text-transform: uppercase;
      font-size: 1.375em;
      font-weight: 500;
      margin-bottom: .78em;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    padding-bottom: 2em;
    .map{
      height: 24em;
      margin-bottom: 1.75em;
    }
  }
}
