.fg {
  position: relative;
  margin: 0;
  .nice-select,
  input,
  textarea{
    border-radius: 0.375em;
    border: $border;
    padding: 0 1.5em;
    height: 3.75em;
    width: 100%;
    font-size: 1em;
    background-color: #fff;
    color: $black;
    &::placeholder {
      color: rgba(0, 0, 0, 0.5);
    }
    &:focus {
      border-color: $black;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.375em;
    white-space: nowrap;
    height: 2.875em;
    padding: 0 .75em 0 1em;
    color: rgba($black, 0.5);
    float: unset;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &:focus{
      border: 1px solid $black;
    }
    &:before{
      content: '';
      order: 3;
      flex-shrink: 0;
      width: .625em;
      height: .5em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.1);
      background: #fff;
      margin-top: 1px;
      border-radius: .375em;
      left: -1px;
      border: $border;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      max-height: 8.375em;
    }
    .option{
      height: unset;
      font-size: 1em;
      line-height: 125%;
      padding: .25em 1em;
      display: flex;
      align-items: center;
      color: $black;
      border: none;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._select-changed{
    .nice-select{
      color: $black;
    }
  }
  &--line{
    input{
      border: none;
      border-bottom: 1px solid rgba(0, 0, 0, 0.8);
      border-radius: 0;
      padding: 0 .5em;
      font-size: 1.125em;
      height: 2.88em;
      font-weight: 500;
    }
  }
}

.delivery-ways__item,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.625em;
    min-height: 1.25em;
    span{
      font-size: .8125em;
      line-height: 150%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid rgba(0, 0, 0, 0.5);
      width: 1.125em;
      height: 1.125em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-radio{
  input:checked~label{
    color: $black;
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    color: rgba($black,.5);
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  max-width: 100%;
  width: 6em;
  display: grid;
  grid-template-columns: 1.875em calc(100% - 1.875em * 2) 1.875em;
  height: 1.875em;
  button,
  input{
    height: 100%;
    width: 100%;
    min-height: 100%;
  }
  input{
    border: none;
    color: $black;
    font-size: .875em;
    text-align: center;
  }
  svg{
    width: .625em;
    height: auto;
  }
  button{
    @include center;
    background-color: #ECECEC;
    border-radius: .25em;
    &:hover{
      background-color: darken(#ECECEC,10%);
    }
  }
}


