@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.75em;
    height: 5.375em;
    border-radius: .1875em;
    text-transform: uppercase;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.125em;
        line-height: 120%;
    }
    &--small{
        height: 2.875em;
        border-radius: .375em;
        text-transform: unset;
        span{
            font-size: 1em;
        }
    }
}


.m-btn-red{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}


.m-btn-green{
    color: #fff;
    background-color: $green;
    &:hover{
        color: #fff;
        background-color: darken($green,10%);
    }
}



@media screen and (max-width: 992px){
    .m-btn{
        height: 4em;
        padding: 0 1.5em;
        &--small{
            height: 2.875em;
        }
        span{
            font-size: 1em;
        }
    }
}
