@import "../vars.scss";
.page-top{
    padding: 2em 0 2.625em;
    .breadcrumbs{
        margin-bottom: 1em;
    }
}

.breadcrumbs {
    a,span{
        font-size: 1.125em;
        line-height: 122%;
    }
    li {
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: 1em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .3125em;
                height: .625em;
                top: .3125em;
                right: 100%;
                margin-right: .375em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
            }
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: 1em 0 1.125em;
        .breadcrumbs{
            margin-bottom: .75em;
        }
    }
}


