.logo{
  width: 21.0625em;
  display: block;
  flex-shrink: 0;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  padding: 2.875em 0;
  position: relative;
  z-index: 20;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &--index{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    color: #fff;
    .header-action{
      &__btn{
        &--search{
          background-image: url(../img/search2.svg);
        }
        &--cart{
          background-image: url(../img/cart2.svg);
        }
        &--phone{
          background-image: url(../img/phone2.svg);
        }
        ._pin{
          background-color: #fff;
          color: $black;
        }
      }
    }
    .burger,
    .nav__show{
      path{
        fill: #fff;
      }
    }
    .nav__inner{
      color: $black;
    }
    .header-search{
      .search-field{
        box-shadow: 0 1px 9px 0 rgba(#fff, 0.15);
      }
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__item,
  &__link{
    @include lastM(0 2.5em 0 0);
  }
  &__sub-link,
  &__show,
  &__link{
    font-size: 1em;
    line-height: 125%;
    text-transform: uppercase;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        fill-opacity: 1;
        fill: $primary;
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    svg{
      width: .375em;
      margin-left: .25em;
    }
  }
  &__item{
    position: relative;
  }
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__sub-link{
    @include lastM(0 0 1em);
  }
  @media screen and (min-width: 993px){
    &__item{
      &:hover{
        .nav__hidden{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    &__hidden{
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      margin-top: -1px;
      padding-top: 1.125em;
      z-index: 2;
      transition: all .5s;
      opacity: 0;
      pointer-events: none;
    }
    &__inner{
      padding: 1em;
      box-shadow: 0 2px 15px rgba(0, 0, 0, 0.1);
      background: #fff;
      border-radius: .625em;
      width: 11.125em;
    }
  }
}

.burger{
  display: none;
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    @include bgImgSize(100%);
    @include center;
    position: relative;
    width: 1.875em;
    height: 1.875em;
    &--search{
      background-image: url(../img/search.svg);
    }
    &--cart{
      background-image: url(../img/cart.svg);
    }
    &--phone{
      background-image: url(../img/phone.svg);
      width: 1.5625em;
      height: 1.5625em;
      margin-right: .6875em;
    }
    ._pin{
      top: .1em;
      right: -.4em;
    }
  }
  &__call{
    display: flex;
    align-items: center;
    margin-left: 1.1875em;
    font-weight: 500;
  }
}

._pin{
  position: absolute;
  height: 1.5em;
  min-width: 1.5em;
  font-size: .625em;
  background-color: $black;
  border-radius: .75em;
  color: #fff;
  @include center;
  padding: 0 .2em;
}

.header-search{
  position: relative;
  z-index: 3;
  margin-right: 1.1875em;
  &._open{
    .search-field{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__btn{
    @include center;
    img{
      width: 100%;
    }
  }
  .search-field{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    top: 50%;
    right: 100%;
    transform: translateY(-50%);
    border-radius: .375em;
    box-shadow: 0 1px 9px 0 rgba($black, 0.15);
    width: 65.625em;
    margin-right: 1.75em;
    input{
      height: 3.125em;
    }
  }
}


@media screen and (max-width: 992px){
  .logo{
    width: 10em;
  }

  .header{
    padding: 1.25em 0;
    &__inner{
      justify-content: flex-end;
      position: relative;
    }
    &__logo{
      margin-right: auto;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
        path{
          fill: $black
        }
      }
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 1.25em 1em 1em;
    z-index: 10;
    overflow-y: auto;
    flex-direction: column;
    align-items: flex-start;
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    svg{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .nav{
    color: $black;
    flex-direction: column;
    align-items: flex-start;
    &__show{
      display: none;
    }
    &__inner{
      align-items: flex-start;
    }
    &__link,
    &__item{
      @include lastM(0 0 1em);
    }
  }

  .header-action{
    &__call{
      .text16{
        display: none;
      }
    }
    &__btn{
      width: 1.5em;
      height: 1.5em;
    }
  }

  .header-search{
    margin-right: .75em;
    position: static;
    .search-field{
      position: absolute;
      width: 100%;
      left: 0;
      top: 100%;
      transform: unset;
      margin-top: .5em;
      input{
        height: 2.5em;
      }
    }
  }
}


