.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .3;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 35em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 3.5em 1em 4em;
        border-radius: .625em;
        display: inline-block;
    }
    .close-btn{
        width: 1.5625em;
        @include center;
        flex-shrink: 0;
        position: absolute;
        top: .75em;
        right: .75em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__info{
        text-align: center;
        @include grid100gap(1.25em);
        max-width: 22.5em;
        margin: 0 auto;
    }
    &__desc{
        opacity: .8;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2.5em 1em 1em;
        }
        &__info{
            gap: .75em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
