.footer{
  padding: 3.5em 0 5em;
  background-color: $gray;
  margin-top: 3.125em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-contacts{
  display: flex;
  align-items: center;
  margin-top: 2em;
  &__link{
    font-size: 1em;
    font-weight: 500;
    display: flex;
    align-items: center;
    margin-right: 2.5em;
    img{
      width: 1.5625em;
      margin-right: .6875em;
    }
  }
}

.footer-nav{
  width: 44em;
  column-count: 3;
  column-gap: 1em;
  max-width: 100%;
  &__link{
    display: inherit;
    width: 100%;
    margin-bottom: 1.5em;
    font-size: 1em;
    line-height: 125%;
    text-transform: uppercase;
    font-weight: 500;
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 2em;
    padding: 2em 0 1em;
    &__inner{
      @include grid100gap(2em);
    }
  }

  .footer-contacts{
    font-size: .875em;
  }

  .footer-nav{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .footer-nav{
    column-count: 1;
  }
}
