.reviews{
  padding: 1.5em 0 3.125em;
}

.reviews-slider{
  .swiper{
    &-slide{
      width: 33.125em;
    }
  }
}

.review-card{
  padding: 2.5em;
  background-color: $gray;
  border-radius: .375em;
  height: 100%;
}

.req{
  padding: 3.125em 0;
  &__grid{
    display: grid;
    grid-template-columns: 30em 40em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__col{
    padding-top: 1.5em;
  }
  &__img{
    display: block;
    width: 100%;
    border-radius: .625em;
  }
}

.req-form{
  @include grid100gap(1.5em);
  margin-top: 2.5em;
  &__nav{
    display: flex;
    margin-top: .5em;
  }
  &__btn{
    padding: 0 6.25em;
  }
}

.contacts{
  padding: 3.125em 0;
  &--page{
    padding-top: 0;
  }
  &__grid{
    display: grid;
    grid-template-columns: 40em calc(100% - 44.75em);
    justify-content: space-between;
    gap: 1.25em;
  }
  &__col2{
    padding-top: 1em;
  }
}

.map{
  height: 31.5em;
  border-radius: .625em;
  overflow: hidden;
}

.address-item{
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: $primary;
  }
}


@media screen and (max-width: 992px){
  .reviews{
    padding: 1em 0 2em;
    overflow: hidden;
  }

  .reviews-slider{
    .swiper{
      &-slide{
        width: 19em;
      }
    }
  }

  .review-card{
    padding: 1.25em;
  }

  .req{
    padding: 2em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 0;
    }
    &__col2{
      display: none;
    }
  }

  .req-form{
    margin-top: 1em;
    gap: 1em;
  }

  .contacts{
    padding: 2em 0;
    &--page{
      padding-top: 0;
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      padding: 0;
    }
  }

  .map{
    height: 25em;
  }
}


@media screen and (max-width: 576px){
  .req-form{
    &__btn{
      width: 100%;
    }
  }

  .map{
    height: 20em;
  }
}
