.cart{
  padding: 0 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 30.5em) 25em;
    justify-content: space-between;
  }
  &__items{
    @include grid100gap(1.25em);
  }
  &__bottom{
    margin-top: 2em;
    text-align: right;
    font-weight: 500;
  }
  &__empty{
    display: none;
  }
  &._empty{
    .cart__items,
    .cart__bottom{
      display: none;
    }
    .cart__empty{
      display: block;
    }
  }
}

.cart-card{
  padding: 1.25em;
  display: grid;
  grid-template-columns: 13.75em calc(100% - 30.75em) 13.5em;
  gap: 1.75em;
  align-items: flex-start;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
  border-radius: .625em;
  position: relative;
  &__col2{
    padding-top: .9375em;
  }
  &__col3{
    padding-top: .9375em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__img{
    @include imgCover(56.3%);
    border-radius: .375em;
  }
  &__title{
    font-weight: 500;
  }
  &__price{
    font-weight: 500;
    line-height: 180%;
  }
  &__delete{
    position: absolute;
    right: 1.25em;
    bottom: 1.25em;
    display: flex;
    align-items: center;
    color: rgba(0, 0, 0, 0.5);
    &:hover{
      color: $black;
    }
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    img{
      width: 1.125em;
      margin-right: .375em;
    }
  }
}

.order-form{
  margin-top: 1.5em;
  &__fields{
    @include grid100gap(1.25em);
  }
  &__btn{
    margin-top: 1.25em;
    height: 3.875em;
    width: 100%;
    border-radius: .375em;
    text-transform: unset;
    span{
      font-size: 1em;
    }
  }
  &__title{
    margin-top: 1.75em;
  }
}

.delivery-ways{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .5em));
  gap: 1em;
  &__item{
    input:not(:checked)~label{
      background-color: $gray;
      border-color: $gray;
      span,
      img{
        opacity: .3;
      }
    }
    label{
      display: flex;
      align-items: center;
      flex-direction: column;
      padding: 1.125em .5em;
      height: 7em;
      border-radius: .375em;
      border: 1px solid #000;
    }
    img,span{

    }
    img{
      height: 3.25em;
      flex-shrink: 0;
    }
    span{
      font-size: .875em;
      line-height: 121%;
    }
  }
}

.delivery-option{
  margin-top: 1em;
  display: none;
  &._active{
    display: block;
  }
  .fg{
    .nice-select{
      white-space: unset;
      height: unset;
      padding: .9375em .875em .9375em 1.25em;
    }
    .current{
      font-size: .8125em;
      line-height: 150%;
    }
    .list{
      max-height: 18em;
    }
    .option{
      font-size: .8125em;
      padding: .625em 1.53em;
    }
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.625em;
  }
}


@media screen and (max-width: 992px){
  .cart{
    padding: 0 0 2em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
    &__bottom{
      margin-top: 1.25em;
    }
  }

  .cart-card{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .cart-card{
    font-size: 1em;
    grid-template-columns: 100%;
    padding: 1em 1em 3em;
    gap: 0;
    &__col{
      max-width: 15em;
    }
    &__delete{
      left: 1em;
      right: unset;
      bottom: 1em;
    }
  }

  .order-form{
    font-size: .875em;
  }
}
