.text h2,
.text h4,
.text h5,
.h1,
.h2,
.h3,
.h4{
  font-weight: 600;
  text-transform: uppercase;
  line-height: 122%;
  letter-spacing: 0;
}

.h1{
  font-size: 2.75em;
}

.text h2,
.h2{
  font-size: 1.75em;
}

.h3{
  font-size: 1.5em;
  font-weight: 500;
}

.text h4,
.h4{
  font-size: 1.375em;
  font-weight: 500;
}

.text20{
  font-size: 1.25em;
  line-height: 125%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text h5,
.text li,
.text p,
.text blockquote,
.text18{
  font-size: 1.125em;
  line-height: 123%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.text{
  h2{
    margin-bottom: 1em;
  }
  blockquote,
  p,
  h4,
  h5{
    margin-bottom: .78em;
  }
  ul,
  ol{
    margin-bottom: .875em;
    @include grid100gap(1.5em);
  }
  ul,
  ol,
  p{
    &:last-child{
      margin-bottom: 0;
    }
  }
  hr{
    display: block;
    margin: 0;
    height: 1.75em;
    opacity: 0;
  }
  ol{
    counter-reset: list;
    li{
      counter-increment: list;
      position: relative;
      padding-left: 1.55em;
      &:before {
        content: counter(list) '. ';
        position: absolute;
        top: 0;
        left: 0;
        padding-left: .25em;
      }
    }
  }
  ul{
    li{
      position: relative;
      padding-left: 1.55em;
      &:before {
        content: '•';
        position: absolute;
        top: 0;
        left: 0;
        padding-left: .3em;
        font-size: 1.75em;
        transform: translateY(-15%);
      }
    }
  }
  blockquote{
    font-style: italic;
    &:before{
      content: '“';
    }
    &:after{
      content: '”';
    }
  }
  table{
    min-width: 100%;
    border-collapse: collapse;
    th,td{
      border: $border;
      padding: 1em;
      font-size: 1em;
      line-height: 160%;
    }
    th{
      text-align: center;
      font-weight: 600;
      background-color: $primary;
      color: #fff;
    }
  }
  &--small{
    p,li{
      font-size: 1em;
      line-height: 160%;
      color: rgba($black,.8);
    }
  }
  &--gap0{
    ol{
      gap: .25em;
    }
    ul{
      gap: .25em;
      ul{
        li{
          font-size: 1em;
        }
      }
    }
  }
}

.ordinary-text{
  text-transform: unset;
}



@media screen and (max-width: 992px){
  .h1{
    font-size: 1.75em;
  }

  .h2{
    font-size: 1.5em;
  }

  .h3,
  .h4{
    font-size: 1.125em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text h5,
  .text p,
  .text li,
  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

  .text{
    ol{
      gap: .75em;
    }
    hr{
      height: 1em;
    }
  }

}


@media screen and (max-width: 576px){
  .text{
    table{
      font-size: .625em;
    }
  }
}
