.tabs{
  display: flex;
}

.tab{
  height: 1.875em;
  padding: 0 .875em;
  display: flex;
  align-items: center;
  position: relative;
  color: rgba($black,.6);
  transition: all .3s;
  &:before{
    content: '';
    position: absolute;
    width: 100%;
    height: 1.2px;
    left: 0;
    bottom: 0;
    background-color: $green;
    opacity: 0;
    transition: inherit;
  }
  span{
    font-size: .9375em;
    line-height: 120%;
  }
  &:hover,
  &._active{
    color: $black;
    &:before{
      opacity: 1;
    }
  }
  &:hover{
    cursor: pointer;
  }
  &._active{
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
