.catalog-item{
  padding: 0 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 23.4375em - 3.75em) 24.25em;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__get{
    margin-top: 4.25em;
    .h4{
      text-transform: unset;
      font-weight: 400;
    }
  }
  .map{
    margin-top: 2.1875em;
    height: 22.25em;
  }
  &._added{
    .product-cart__btn{
      background-color: darken($gray,20%);
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.product-info{
  display: grid;
  grid-template-columns: 28em 25em;
  justify-content: space-between;
  gap: 1.25em;
  &__col2{
    padding-top: .375em;
  }
}

.gallery{
  &__big{
    max-width: 25em;
  }
  &__small{
    overflow: hidden;
    margin-top: .875em;
    .swiper-slide{
      width: 9.6875em;
      padding-right: 1em;
    }
  }
}

.gallery-item{
  display: block;
  &--big{
    grid-column: 1/-1;
    max-width: 25em;
  }
  &__img{
    @include imgCover(56.5%);
    border-radius: .375em;
  }
}

.props{
  @include grid100gap(.875em);
}

.prop-item{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .25em));
  gap: .5em;
  align-items: flex-start;
  &__title,
  &__desc{
    font-size: .9375em;
    line-height: 140%;
    min-height: 1.8em;
    display: flex;
    align-items: center;
  }
  &__title{
    opacity: .6;
  }
}

.product-cart{
  padding: 1.25em 1.125em 2.1875em;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.1);
  background: #fff;
  border-radius: .625em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price{
    line-height: 180%;
    font-weight: 500;
  }
  &__available{
    color: $green;
  }
  &__btn{
    width: 100%;
    margin-top: .625em;
    ._view2{
      display: none;
    }
  }
}

.product-delivery{
  margin-top: 1.75em;
  &__title{
    line-height: 180%;
  }
  .tabs{
    margin-top: 1.125em;
    justify-content: space-between;
    max-width: 21em;
  }
  .tab-content{
    margin-top: 1.125em;
  }
}

.courier{
  display: grid;
  grid-template-columns: 50% 50%;
  max-width: 26em;
  &__item{
    border-top: 1px solid rgba(0, 0, 0, 0.3);
    height: 4.75em;
    @include center;
    padding: .625em 1.25em;
    &:nth-child(odd){
      border-right: 1px solid rgba(0, 0, 0, 0.3);
    }
    &:nth-child(1),
    &:nth-child(2){
      border-top: none;
    }
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.self-delivery{
  font-size: .8125em;
  line-height: 150%;
  @include grid100gap(1em);
  & * {
    line-height: inherit;
  }
}

.post{
  display: flex;
  justify-content: center;
  max-width: 21em;
  img{
    width: 9.75em;
    max-width: 100%;
    display: block;
  }
}

.advantages{
  padding: 1.5em 2em;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: .625em;
  margin-top: 2em;
  @include grid100gap(.875em);
}

.advantage-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 1.75em;
    height: 1.75em;
    @include center;
    margin-right: .4375em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .product-page{
    .footer{
      padding-bottom: 6.5em;
    }
  }

  .catalog-item{
    padding: 0 0 2em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__get{
      margin-top: 2em;
    }
    .map{
      margin-top: 1em;
    }
  }

  .product-info{
    font-size: .75em;
    grid-template-columns: 28em calc(100% - 29.25em);
  }

  .product-cart{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    border-radius: .625em .625em 0 0;
    bottom: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    padding: 1em;
    &__top{
      flex-direction: column;
      align-items: flex-start;
      flex-shrink: 0;
    }
    &__btn{
      width: auto;
      flex-grow: 1;
      margin: 0 0 0 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    .map{
      height: 19em;
    }
  }

  .product-info{
    grid-template-columns: 100%;
  }

  .gallery{
    &__big{
      max-width: 100%;
    }
  }

  .advantages{
    padding: 1em;
    font-size: .875em;
  }
}
