.catalog{
  padding: 1.5em 0 3.125em;
  &__grid{
    display: grid;
    grid-template-columns: 13.75em calc(100% - 14.75em);
    gap: 1em;
  }
  &__top{
    .catalog__col{
      padding-top: .375em;
    }
  }
  .page-top{
    padding: 0;
  }
  &__bottom{
    margin-top: 2em;
    .catalog__col{
      padding-top: 1em;
    }
  }
  &__items{
    @include grid100gap(1.25em);
  }
}

.filter{
  padding: 1.5em;
  background-color: $gray;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
  gap: .625em;
  border-radius: .625em;
  &__btn{
    height: 2.875em;
    border-radius: .375em;
    text-transform: unset;
    span{
      font: 1em;
    }
  }
}

.catalog-nav{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1.125em;
  &__link{
    font-size: 1.125em;
    line-height: 122%;
    @include lastM(0 0 .78em);
  }
}

.catalog-card{
  padding: 1.25em 2.25em 2.75em 1.25em;
  border-radius: .625em;
  background-color: $gray;
  display: grid;
  grid-template-columns: 22em calc(100% - 23.5em);
  gap: 1.5em;
  &__img{
    @include imgCover(56.8%);
    border-radius: .375em;
  }
  &__col{
    padding-bottom: 3em;
  }
  &__col2{
    padding-top: 1.125em;
  }
  &__props{
    margin-top: 1.375em;
    @include grid100gap(.3125em);
  }
  &__prop{
    font-size: .9375em;
    line-height: 180%;
    color: rgba($black, 0.7);
    span{
      color: $black;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 2.1875em;
  }
  &__price{
    font-weight: 500;
  }
  &__btn{
    width: 15.875em;
    margin-left: 1.5em;
    ._view2{
      display: none;
    }
    &._added{
      background-color: darken($gray,20%);
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.catalog-slider{
  .swiper{
    &-container{
      width: 100%;
      overflow: hidden;
      border-radius: .375em;
    }
    &-slide{
      width: 100%;
      padding: 0;
    }
    &-pagination{
      position: static;
      margin-top: .6875em;
      &-bullet{
        background-color:  rgba(0, 0, 0, 0.15);
        opacity: 1;
        width: .5em;
        height: .5em;
        margin: 0 .25em !important;
        &-active{
          background-color: $green;
        }
      }
    }
  }
}

.search{
  padding-bottom: 3.125em;
  .catalog__items{
    max-width: 69.625em;
  }
}

.error-page{
  padding: 2em 1em;
  @include center;
  flex-direction: column;
  text-align: center;
  flex-grow: 1;
  &__title{
    font-size: 7.5em;
    line-height: 122%;
    font-weight: 600;
  }
  &__link{
    margin-top: 1.875em;
    width: 15.875em;
    max-width: 100%;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 1em 0 2em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__bottom{
      margin-top: 1.5em;
      gap: 1.5em;
      .catalog__col{
        padding: 0;
      }
    }
  }

  .filter{
    padding: 1em;
    grid-template-columns: repeat(2,calc(50% - .3125em));
  }

  .catalog-card{
    font-size: .875em;
    padding: 1em;
    &__prop{
      line-height: 140%;
      font-size: .875em;
    }
  }

  .search{
    padding-bottom: 2em;
  }

  .error-page{
    &__title{
      font-size: 6.25em;
    }
    &__desc{
      font-size: 1em;
    }
    &__link{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .filter{
    grid-template-columns: 100%;
  }

  .catalog-card{
    grid-template-columns: 100%;
    gap: 0;
    &__col{
      padding: 0;
    }
    &__props{
      margin-top: 1em;
    }
    &__bottom{
      display: block;
      margin-top: 1em;
    }
    &__btn{
      width: 100%;
      margin: .5em 0 0;
    }
  }
}
